.tab-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 62px;
  background: #000;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 2px solid #222;
  z-index: 100;
  box-shadow: 0 -2px 16px 0 #000;
  border-radius: 0 0 18px 18px;
}
.tab-bar button {
  background: none;
  border: none;
  color: #bbb;
  font: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1 1 0;
  height: 100%;
  font-size: 1.2em;
  cursor: pointer;
  opacity: 0.8;
  transition: opacity 0.2s, color 0.2s;
}
.tab-bar button.active, .tab-bar button:focus {
  opacity: 1;
  color: #fff;
}
.tab-bar span {
  font-size: 14px;
  line-height: 32px;
}
.tab-bar button svg {
  width: 24px;
  height: 24px;
  display: block;
  margin: 0 auto 2px auto;
}
.tab-bar button span:last-child {
  font-size: 0.95em;
  margin-top: 2px;
  font-weight: 600;
}
